import React from 'react'
import Link from 'gatsby-link'
const Header = () => (
  <div id="headerWrapper">
    <header
      style={{
        margin: '0 auto',
      }}>
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            color: '#FFF',
            textDecoration: 'none',
          }}
        >
          
        </Link>
      </h1>
    </header>
  </div>
)

export default Header



